@font-face {
  font-family: Lora;
  src: url("../fonts/lora-regular.ttf");
}

* {
  margin: 0 0;
  padding: 0 0;
  box-sizing: border-box;
}

@-webkit-viewport {
  width: device-width;
}

@-moz-viewport {
  width: device-width;
}

@-ms-viewport {
  width: device-width;
}

@-o-viewport {
  width: device-width;
}

@viewport {
  width: device-width;
}

html, body {
  font-size: 12pt;
  color: #333;
  height: 100%;
  font-family: "Lora", arial, sans-serif;
  background-color: #f2f2f2;
}

.flex-wrapper {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
}

.nav {
  position: fixed;
  top: 10%;
  width: 20%;
  max-width: 200px;
  height: 300px;
}

@media (min-width: 1600px) {
  .nav {
    margin-left: 2rem;
  }
}

@media (min-width: 2000px) {
  .nav {
    margin-left: 4rem;
  }
}

.nav .nav_h1 {
  width: 80%;
  height: 0;
  padding-bottom: 60%;
  margin: 0 auto;
  background-image: url("../images/signature.png");
  background-size: cover;
}

.nav ul {
  padding-top: 1rem;
  list-style: none;
  text-align: center;
}

.nav ul li {
  line-height: 1.5rem;
  position: relative;
}

.nav ul .nav_hr {
  margin-top: 1rem;
}

.nav ul .nav_hr:before {
  content: '';
  position: absolute;
  top: -0.5rem;
  left: 45%;
  height: 1px;
  width: 10%;
  min-width: 10px;
  border-bottom: 1px solid #999;
}

.float-pusher {
  width: 20%;
  height: 100vh;
  max-width: 200px;
  float: left;
}

main {
  position: relative;
  margin-top: 10%;
  width: 80%;
  max-width: 1200px;
  min-height: 90vh;
  float: left;
  padding: 1rem;
}

@media (min-width: 1600px) {
  main {
    max-width: 1200px;
    margin-left: 4rem;
  }
}

@media (min-width: 2000px) {
  main {
    max-width: 1600px;
    margin-left: 8rem;
  }
}

.grid-item-2 {
  width: 66%;
}

.grid-item {
  width: 30%;
  padding: 1rem;
}

.grid-sizer {
  width: 33%;
}

.gallery-overlay {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(51, 51, 51, 0.6);
}

.gallery-overlay .gallery-modal {
  position: relative;
  height: auto;
  width: auto;
  margin: 0 auto;
  background-color: #fff;
}

.modal-close {
  display: none;
  position: absolute;
  top: -5px;
  right: -7px;
  background-color: #fff;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.9), transparent, transparent);
  height: 60px;
  width: 60px;
  z-index: 200;
  cursor: pointer;
}

.modal-close:after {
  position: relative;
  content: '\e900';
  font-family: 'icomoon' !important;
  color: #333;
  font-size: 2rem;
  top: 13px;
  left: 14px;
}
